        </b-col>
        <b-col md="6">
          <b-form-group label="* Sub Department" label-for="sub-department">
            <b-form-select id="sub-department" v-model="sub_department">
              <option value="0">Select Sub Department</option>
              <option value="1">IT</option>
              <option value="2">HR</option>
              <option value="3">Finance</option>
              <option value="4">Marketing</option>
              <option value="5">Sales</option>
              <option value="6">Operation</option>
              <option value="7">Others</option>
            </b-form-select>
          </b-form-group>
        </b-col>
        <b-col md="6">
          <b-form-group label="* Job Level" label-for="job-level">
            <b-form-select id="job-level" v-model="job_level">
