<script>
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";
import Ripple from "vue-ripple-directive";

export default {
  data() {
    return {
      departments: [],
      department: "",
      sub_department: "",
      job_level: "",
      job_position: "",
      employee_type: "",
    };
  },
  directives: {
    Ripple,
  },
  mounted() {
    this.callApi({
      method: "GET",
      url: "/department",
      params: {
        with_responsibilities: 1,
      },
      success: (response) => {
        this.departments = response.result.data;
      },
      error: (error) => {
        this.$toast({
          component: ToastificationContent,
          props: {
            title: "Error",
            message: "Job & Department data not loaded",
            type: "error",
          },
        });
      },
    });
  },
  computed: {},
  methods: {},
};
</script>
