                    static: true,
                  }"
                  class="form-control"
                />
              </b-form-group>
            </b-col>
            <b-col md="4">
              <b-form-group label="* End Date" label-for="end-date">
                <flat-pickr
                  id="end-date"
                  v-model="end_date"
                  class="form-control"
                  :config="{
                    dateFormat: 'Y-m-d',
                    altInput: true,
                    altFormat: 'F j, Y',
                    allowInput: true,
                    static: true,
                  }"
                />
              </b-form-group>
            </b-col>
            <b-col md="4">
              <b-form-group label="* Grade" label-for="grade">
                <b-form-input id="grade" v-model="grade" />
              </b-form-group>
            </b-col>
            <b-col md="12">
              <b-form-group
                label="Activities Societis"
                label-for="activities-societis"
              >
                <b-form-textarea
                  id="activities-societis"
                  v-model="activities_societis"
                />
              </b-form-group>
            </b-col>
            <b-col md="12">
              <label for="dropzone"
                >Files, ex: Ijazah or any and must be type img/pdf and max size
                is 6Mb
              </label>
              <vue-dropzone :options="dropzoneOptions" />
            </b-col>
            <b-col cols="12" class="text-right mt-3">
              <b-button
                v-ripple.400="'rgba(255, 255, 255, 0.15)'"
                variant="primary"
                type="submit"
              >
                <feather-icon size="16" icon="SaveIcon" class="mr-50" />
                Save
              </b-button>
            </b-col>
          </b-row>
        </form>
      </div>
    </b-modal>

    <b-modal
      v-for="education in educations"
      :key="education.id"
      :id="'update_' + education.id"
      hide-footer
      no-close-on-backdrop
      centered
      size="lg"
      data-bs-focus="false"
    >
      <div class="px-3 pb-3 pt-1">
        <div class="text-center mb-3" style="line-height: 1px">
          <h1 class="mb-2">Detail Education History</h1>
          <span>
            The last historical education will be used for further data
          </span>
        </div>
        <form @submit.prevent="edit_education(education)">
          <b-row>
            <b-col md="4">
              <b-form-group label="* Institute Name" label-for="institute-name">
                <b-form-input
                  id="institute-name"
                  v-model="education.institution_name"
                />
              </b-form-group>
            </b-col>
            <b-col md="4">
              <b-form-group label="* Degree" label-for="degree">
                <b-form-select id="Degree" v-model="education.degree">
                  <option value="">Select Degree</option>
                  <option
                    value="junior"
                    :if="education.degree == 'junior' ? 'selected' : ''"
                    label="Junior High School"
                  ></option>
                  <option
                    value="senior"
                    :selected="education.degree == 'senior'"
                    label="Senior High School"
                  ></option>
                  <option
                    value="diploma"
                    :selected="education.degree == 'diploma'"
                    label="Diploma"
                  ></option>
                  <option
                    value="associate"
                    :selected="education.degree == 'associate'"
                    label="Associate"
                  ></option>
                  <option
                    value="bachelor"
                    :selected="education.degree == 'bachelor'"
                    label="Bachelor"
                  ></option>
                  <option
                    value="master"
                    :selected="education.degree == 'master'"
                    label="Master"
                  ></option>
                  <option
                    value="doctor"
                    :selected="education.degree == 'doctor'"
                    label="Doctor"
                  ></option>
                </b-form-select>
              </b-form-group>
            </b-col>
            <b-col md="4">
              <b-form-group label="* Field of Study" label-for="field-of-study">
                <b-form-input
                  id="field-of-study"
                  v-model="education.field_of_study"
                />
              </b-form-group>
            </b-col>
            <b-col md="4">
              <b-form-group label="* Start Date" label-for="start-date">
                <flat-pickr
                  id="start-date"
                  v-model="education.start_date"
                  :config="{
                    dateFormat: 'Y-m-d',
                    altInput: true,
                    altFormat: 'F j, Y',
                    allowInput: true,
                    static: true,
                  }"
                  class="form-control"
                />
              </b-form-group>
            </b-col>
            <b-col md="4">
              <b-form-group label="* End Date" label-for="end-date">
                <flat-pickr
                  id="end-date"
                  v-model="education.end_date"
                  class="form-control"
                  :config="{
                    dateFormat: 'Y-m-d',
                    altInput: true,
                    altFormat: 'F j, Y',
                    allowInput: true,
                    static: true,
                  }"
                />
              </b-form-group>
            </b-col>
            <b-col md="4">
              <b-form-group label="* Grade" label-for="grade">
                <b-form-input id="grade" v-model="education.grade" />
              </b-form-group>
            </b-col>
            <b-col md="12">
              <b-form-group
                label="Activities Societis"
                label-for="activities-societis"
              >
                <b-form-textarea
                  id="activities-societis"
                  v-model="education.activities_societis"
                />
              </b-form-group>
            </b-col>
            <b-col md="12">
              <label for="dropzone"
                >Files, ex: Ijazah or any and must be type img/pdf and max size
                is 6Mb
              </label>
              <vue-dropzone :options="dropzoneOptions" />
            </b-col>
            <b-col cols="12" class="text-right mt-3">
              <b-button
                v-ripple.400="'rgba(255, 255, 255, 0.15)'"
                variant="outline-danger"
                type="submit"
                class="mr-1"
                @click="delete_education(education.id)"
              >
                <feather-icon size="16" icon="TrashIcon" class="mr-50" />
                Delete
              </b-button>
              <b-button
                v-ripple.400="'rgba(255, 255, 255, 0.15)'"
                variant="primary"
                type="submit"
              >
                <feather-icon size="16" icon="SaveIcon" class="mr-50" />
                Update
              </b-button>
            </b-col>
