</template>

<style>
.modal .modal-header {
  background: none !important;
}

.dropzone {
  border: 2px dashed #0561bd;
  border-radius: 0.25rem;
  background: #fff;
  min-height: 50px;
  padding: 0rem !important;
  text-align: center;
  color: #adb5bd;
  font-size: 1.5rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.dropzone .dz-message {
  font-size: 1.5rem;
  font-weight: 500;
  padding: 0px;
  color: #adb5bd;
}
</style>

<script>
import {
  BButton,
  BForm,
  BFormGroup,
  BFormInput,
  BFormSelect,
  BFormTextarea,
  BFormCheckbox,
  BRow,
  BCol,
  BCard,
  BCardBody,
  BInputGroup,
  BInputGroupAppend,
  BModal,
  VBModal,
} from "bootstrap-vue";
import Ripple from "vue-ripple-directive";
import flatPickr from "vue-flatpickr-component";
import AppTimeline from "@core/components/app-timeline/AppTimeline.vue";
import AppTimelineItem from "@core/components/app-timeline/AppTimelineItem.vue";
import axios from "axios";
import vue2Dropzone from "vue2-dropzone";
import "vue2-dropzone/dist/vue2Dropzone.min.css";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";

export default {
  components: {
    vueDropzone: vue2Dropzone,
    flatPickr,
    BModal,
    BCardBody,
    VBModal,
    AppTimeline,
    AppTimelineItem,
    BFormCheckbox,
    BFormSelect,
    BFormTextarea,
    BButton,
    BForm,
    BFormGroup,
    BFormInput,
    BRow,
    BCol,
    BCard,
    BInputGroup,
    BInputGroupAppend,
    ToastificationContent,
  },
  directives: {
    Ripple,
    "b-modal": VBModal,
  },
  data() {
    return {
      userData: JSON.parse(this.getCookie("sw_auth_data")),

      educations: [],
      institute_name: "",
      degree: "",
      field_of_study: "",
      start_date: "",
      end_date: "",
      grade: "",
      activities_societis: "",

      dropzoneOptions: {
        url: "https://httpbin.org/post",
        thumbnailWidth: 150,
        maxFilesize: 6,
        headers: { "My-Awesome-Header": "header value" },
      },
    };
  },
  mounted() {
    this.callApi({
      method: "GET",
      url: "/education",
      params: {
        user_id: this.userData.id,
      },
      success: (response) => {
        this.educations = response.result;
      },
      error: (error) => {
        console.log(error);
      },
    });
  },
  computed: {},
  methods: {
    add_education() {
      axios({
        method: "post",
        url: "https://sw-api.hracademy.id/api/education",
        headers: {
          Authorization: `Bearer ${localStorage.getItem("sw_token")}`,
        },
        data: {
          user_id: this.userData.id,
          institution_name: this.institute_name,
          degree: this.degree,
          field_of_study: this.field_of_study,
          start_date: this.start_date,
          end_date: this.end_date,
          grade: this.grade,
          activities_societis: this.activities_societis,
        },
      })
        .then((response) => {
          this.educations.push(response.data.result);
          this.$bvModal.hide("add-education");
        })
        .catch((error) => {
          console.log(error);
        });
    },

    edit_education(education) {
      this.callApi({
        method: "POST",
        url: `/education/update/${education.id}`,
        data: {
          user_id: this.userData.id,
          institution_name: education.institution_name,
          degree: education.degree,
          field_of_study: education.field_of_study,
          start_date: education.start_date,
          end_date: education.end_date,
          grade: education.grade,
          activities_societis: education.activities_societis,
        },
        success: (response) => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: "Success",
              text: "Education has been updated",
              icon: "CheckIcon",
              variant: "success",
            },
          });

          this.$bvModal.hide("update_" + education.id);
        },
        error: (error) => {
          console.log(error);
        },
      });
    },

    delete_education(id) {
      axios({
        method: "delete",
        url: `https://sw-api.hracademy.id/api/education/${id}`,
        headers: {
          Authorization: `Bearer ${localStorage.getItem("sw_token")}`,
        },
      })
        .then((response) => {
          this.educations = this.educations.filter((education) => {
            return education.id != id;
          });
          this.$bvModal.hide("update_" + id);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
<style>
@import url("https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");
