<template>
  <div>
    <b-card>
      <div class="d-flex justify-content-between align-items-center mb-1">
        <div class="card-title">
          Experience History <br />
          <span style="font-size: 14px; color: darkgray">
            The last historical experience will be used for further data
          </span>
        </div>
        <button
          v-ripple.400="'rgba(255, 255, 255, 0.15)'"
          class="btn btn-primary d-flex align-items-center"
          v-b-modal.add-education
        >
          <feather-icon size="16" icon="PlusIcon" class="mr-50" /> Add
        </button>
      </div>
      <b-card-body>
        <app-timeline>
          <app-timeline-item
            v-for="(experience, index) in experiences"
            :key="index"
            :title="experience.company_name"
            :subtitle="experience.job_title"
            :from_date="experience.start_date"
            :to_date="experience.end_date"
            :target="'update_' + experience.id"
            variant="success"
          />
        </app-timeline>
      </b-card-body>
    </b-card>
    <b-row class="d-none">
      <b-col md="6">
        <b-form-group label="* Company Name" label-for="company-name">
          <b-form-input id="company-name" v-model="company_name" />
        </b-form-group>
      </b-col>
      <b-col md="6">
        <b-form-group label="* Job Title" label-for="job-title">
          <b-form-input id="job-title" v-model="job_title" />
        </b-form-group>
      </b-col>
      <b-col md="6">
        <b-form-group label="* Employment Type" label-for="employmeny-type">
          <b-form-select id="employmeny-type" v-model="employment_type">
            <option value="full-time">Full Time</option>
            <option value="part-time">Part Time</option>
            <option value="contract">Contract</option>
            <option value="internship">Internship</option>
            <option value="volunteer">Volunteer</option>
            <option value="other">Other</option>
          </b-form-select>
        </b-form-group>
      </b-col>
      <b-col md="6">
        <b-form-group label="* Location" label-for="location">
          <b-form-input id="location" v-model="location" />
        </b-form-group>
      </b-col>
      <b-col md="12">
        <b-form-group>
          <b-form-checkbox
            v-model="currently_working"
            id="currently-working"
            v-if="!end_currently_working"
          >
            I am currently working here
          </b-form-checkbox>
        </b-form-group>
      </b-col>
      <b-col md="6">
        <b-form-group label="* Start Date" label-for="start-date">
          <flat-pickr
            id="start-date"
            v-model="start_date"
            :config="{
              dateFormat: 'Y-m-d',
              altInput: true,
              altFormat: 'F j, Y',
              allowInput: true,
            }"
          />
        </b-form-group>
      </b-col>
      <b-col md="6" v-if="!currently_working">
        <b-form-group label="* End Date" label-for="end-date">
          <flat-pickr
            id="end-date"
            v-model="end_date"
            class="form-control"
            :config="{
              dateFormat: 'Y-m-d',
              altInput: true,
              altFormat: 'F j, Y',
              allowInput: true,
            }"
          />
        </b-form-group>
      </b-col>
      <b-col md="12">
        <b-form-group>
          <b-form-checkbox
            v-model="end_currently_working"
            @change="changeCurrentlyWorking()"
            id="end-currently-working"
          >
            End currently working now
          </b-form-checkbox>
        </b-form-group>
      </b-col>
      <b-col md="12">
        <b-form-group label="Description" label-for="description">
          <b-form-textarea id="description" v-model="description" />
        </b-form-group>
      </b-col>
      <b-col md="12">
        <b-form-group
          label="Optional: documents, photos, sites, videos, and presentations."
          label-for="media"
        >
          <b-form-file
            id="media"
            v-model="media"
            placeholder="Choose a file or drop it here..."
            drop-placeholder="Drop file here..."
            accept="image/*,application/pdf,.doc,.docx,.ppt,.pptx,.odt,.xls,.xlsx,.txt,.rtf,.zip,.rar,.7z,.tar,.gz,.mp4,.mp3,.wav,.avi,.mov,.wmv,.flv,.mkv,.3gp,.webm,.ogg,.ogv,.m4v,.m4a,.mpg,.mpeg,.swf,.wma,.aac,.flac,.m4b,.m4r,.m4p,.m3u,.m3u8,.pls,.cue,.aif,.aiff,.aifc,.dm"
            multiple
          ></b-form-file>
        </b-form-group>
      </b-col>
      <b-col md="12" class="mt-2">
        <b-form-group>
          <b-button variant="primary" class="float-right">
            Update Career Experience
          </b-button>
        </b-form-group>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { BModal, VBModal } from "bootstrap-vue";
import Ripple from "vue-ripple-directive";
import flatPickr from "vue-flatpickr-component";
import AppTimeline from "@core/components/app-timeline/AppTimeline.vue";
import AppTimelineItem from "@core/components/app-timeline/AppTimelineItem.vue";
import axios from "axios";
import vue2Dropzone from "vue2-dropzone";
import "vue2-dropzone/dist/vue2Dropzone.min.css";

export default {
  components: {
    vueDropzone: vue2Dropzone,
    flatPickr,
    BModal,
    VBModal,
    AppTimeline,
    AppTimelineItem,
  },
  directives: {
    Ripple,
    "b-modal": VBModal,
  },
  props: {},
  data() {
    return {
      userData: JSON.parse(this.getCookie("sw_auth_data")),
      experiences: [],

      company_name: "",
      job_title: "",
      employment_type: "",
      location: "",
      start_date: "",
      end_date: "",
      currently_working: false,
      end_currently_working: false,
      description: "",
      media: [],
    };
  },
  mounted() {
    this.callApi({
      method: "GET",
      url: "/career_experience",
      params: {
        user_id: this.userData.id,
      },
      success: (response) => {
        this.experiences = response.result;
      },
      error: (error) => {
        console.log(error);
      },
    });
  },
  computed: {},
  methods: {
    changeCurrentlyWorking() {
      if (this.end_currently_working) {
        this.currently_working = false;

        this.end_date = this.dateNow();
      } else {
        this.end_date = "";
      }
    },

    dateNow() {
      var today = new Date();
      var dd = today.getDate();
      var mm = today.getMonth() + 1; //January is 0!
      var yyyy = today.getFullYear();

      if (dd < 10) {
        dd = "0" + dd;
      }

      if (mm < 10) {
        mm = "0" + mm;
      }

      today = yyyy + "-" + mm + "-" + dd;
      return today;
    },
  },
};
</script>
