            <b-form-input
              id="fullname"
              v-model="name"
              placeholder="Fullname"
              name="fullname"
            />
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="Gender" label-for="gender">
            <b-form-select id="gender" v-model="gender" name="gender" required>
              <option value="MALE">Male</option>
              <option value="FEMALE">Female</option>
            </b-form-select>
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="Birthday" label-for="birthday">
            <flat-pickr
              v-model="birthday"
              :config="{
                dateFormat: 'Y-m-d',
                altInput: true,
                altFormat: 'F j, Y',
                allowInput: true,
              }"
            />
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="Birthplace" label-for="birthplace">
            <b-form-input
              id="birthplace"
