        <b-col sm="6" md="4">
          <b-form-group label="Branch" label-for="branch">
            <b-form-select id="branch" v-model="branch" name="branch">
              <option
                v-for="branch in branches"
                :value="branch.id"
                :key="branch.id"
                :selected="branch.id === branch"
              >
                {{ branch.name }}
              </option>
            </b-form-select>
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group
