            >
              <option
                v-for="educationBackground in educationBackgrounds"
                :value="educationBackground.id"
                :key="educationBackground.id"
                :selected="educationBackground.id === education_background"
              >
                {{ educationBackground.name }}
              </option>
            </b-form-select>
          </b-form-group>
        </b-col>
        <b-col sm="6" md="4">
          <b-form-group label="Name of Institution" label-for="name-of-instute">
            <b-form-input
              id="name-of-instute"
              v-model="name_of_institution"
