              name="name_of_instute"
            ></b-form-input>
          </b-form-group>
        </b-col>

        <b-col cols="12" class="text-right">
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            variant="primary"
            class="mt-3"
            type="submit"
          >
            Save changes
          </b-button>
        </b-col>
      </b-row>
    </b-form>
  </b-card>
</template>

<script>
import axios from "axios";
import { BMedia, BMediaAside, BMediaBody } from "bootstrap-vue";
import Ripple from "vue-ripple-directive";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";

export default {
  components: {
    ToastificationContent,
    BMedia,
    BMediaAside,
    BMediaBody,
  },
  directives: {
    Ripple,
  },
  props: {
    generalData: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      profileFile: null,

      id: "",
      avatar: "",
      hrcode: "",
      name: "",
      gender: "",
      birthday: "",
      birthplace: "",
      marital_status: "",
      dependent: "",
      nationality: "",
      religion: "",
      number_identification: "",
      place_of_issue: "",
      branch: "",
      education_background: "",
      name_of_institution: "",
      email: "",

      nationalities: [
        {
          id: 1,
          name: "Indonesia",
        },
        {
          id: 2,
          name: "Singapura",
        },
        {
          id: 3,
          name: "Malaysia",
        },
      ],

      branches: [
        {
          id: 1,
          name: "Jakarta",
        },
        {
          id: 2,
          name: "Bandung",
        },
        {
          id: 3,
          name: "Surabaya",
        },
      ],

      educationBackgrounds: [
        {
          id: 1,
          name: "Junior High School",
        },
        {
          id: 2,
          name: "Senior High School",
        },
        {
          id: 3,
          name: "Diploma",
        },
        {
          id: 4,
          name: "Associate Degree",
        },
        {
          id: 5,
          name: "Bachelor",
        },
        {
          id: 6,
          name: "Master",
        },
        {
          id: 7,
          name: "Doctoral",
        },
      ],

      religions: [
        {
          id: 1,
          name: "Islam",
        },
        {
          id: 2,
          name: "Kristen",
        },
        {
          id: 3,
          name: "Katolik",
        },
        {
          id: 4,
          name: "Hindu",
        },
        {
          id: 5,
          name: "Budha",
        },
        {
          id: 6,
          name: "Konghucu",
        },
      ],
    };
  },

  created() {
    this.callApi({
      method: "GET",
      url: "/users/fetch?id=1",
      success: (res) => {
        this.id = res.result.id;
        this.avatar = res.result.profile_photo_url;
        this.hrcode = res.result.hrcode;
        this.name = res.result.name;
        this.email = res.result.email;
        this.gender = res.result.gender;
        this.birthday = res.result.birthday;
        this.birthplace = res.result.birthplace;
        this.marital_status = res.result.marital_status;
        this.dependent = res.result.dependent;
        this.nationality = res.result.nationality;
        this.religion = res.result.religion;
        this.number_identification = res.result.number_of_identity;
        this.place_of_issue = res.result.place_of_identity;
        this.branch = res.result.branch;
      },
      error: (err) => {
        console.log(err);
      },
    });
  },

  methods: {
    setPreview() {
      const file = this.$refs.profileFile.$el.files[0];
      const reader = new FileReader();
      reader.onload = (e) => {
        this.avatar = e.target.result;
      };
      reader.readAsDataURL(file);
    },

    submitForm() {
      axios({
        method: "POST",
        headers: {
          Authorization: "Bearer " + localStorage.getItem("sw_token"),
        },
        url: "https://sw-api.hracademy.id/api/employee?id=" + this.id,
        data: {
          name: this.name,
          gender: this.gender,
          email: this.email,
          birthday: this.birthday,
          birthplace: this.birthplace,
          marital_status: this.marital_status,
          dependent: this.dependent,
          nationality: this.nationality,
          religion: this.religion,
          number_of_identity: this.number_identification,
          place_of_identity: this.place_of_issue,
          branch: this.branch,
          age: 20,
          phone: "0819281923",
          team_id: 1,
          role_id: 1,
        },
      });
    },
  },
};
</script>
